@font-face {
  font-family: "Montserrat-Light";
  src: url("/public/assets/fonts/Montserrat-Light.ttf");
}

@font-face {
  font-family: "Montserrat-Medium";
  src: url("/public/assets/fonts/Montserrat-Medium.ttf");
