body {
  background: black !important;
  overflow: hidden;
}

.yl-content {
  height: 100vh;
  overflow-x: hidden;
  overflow-y: hidden;
}

.nav-menu {
  left: 0;
  right: 0;
  position: fixed;
  z-index: 1;
}

.minimize-content .yl-home {
  display: none;
}

.minimize-content .nav-menu {
  top: 0;
  left: 0;
  right: 0;
  align-items: flex-end;
}

.list-menu {
  display: flex;
  justify-content: center;
  padding-top: 15px;
  padding-bottom: 15px;
}

.minimize-content .list-menu {
  justify-content: flex-end;
  padding: 0;
}

.nav-menu .list-menu .nav-link {
  text-transform: capitalize;
  font-family: Montserrat-Medium;
  color: #ffffff;
  cursor: pointer;
  opacity: 0.8;
  font-size: 18px;
}

.nav-menu .list-menu .nav-link.link-close {
  display: none;
}

.nav-menu .list-menu .nav-link:hover {
  opacity: 1;
}

.minimize-content .nav-menu .list-menu .nav-link {
  font-size: 15px;
}

.minimize-content .nav-menu .list-menu .nav-link.link-close {
  display: block;
}

.minimize-content .nav-menu .list-menu .nav-link.active {
  color: #434343;
  opacity: 1;
}

@media (max-width: 1200px) {
  .yl-content {
    overflow-y: auto;
  }

  .minimize-content .yl-home {
    display: block;
  }

  .nav-menu,
  .minimize-content .nav-menu {
    background: rgba(0, 0, 0, 0.9);
    left: -300px;
    right: auto;
    top: 0;
    bottom: 0;
    width: 300px;
    padding: 25px 15px;
    z-index: 1024;
  }

  .nav-menu.show {
    transition: 0.6s;
    left: 0px;
  }

  .nav-menu .btn_toggle {
    position: fixed;
    left: 0px;
    top: 15px;
    background: rgba(0, 0, 0, 0.9);
    padding: 5px 15px;
  }

  .nav-menu .btn_toggle:before {
    content: "\f036";
    font-family: FontAwesome;
    color: #ffffff;
    opacity: 0.8;
    font-size: 25px;
    cursor: pointer;
  }

  .nav-menu.show .btn_toggle {
    left: 300px;
    transition: 0.6s;
  }

  .nav-menu.show .btn_toggle:before {
    content: "\f038";
  }

  .nav-menu .list-menu {
    flex-direction: column;
  }

  .minimize-content .nav-menu .list-menu .nav-link,
  .nav-menu .list-menu .nav-link {
    font-size: 19px;
    font-family: Montserrat-Bold;
  }

  .minimize-content .nav-menu .list-menu .nav-link.link-close,
  .nav-menu .list-menu .nav-link.link-close {
    display: none;
  }
}

