.progress-circle {
  position: relative;
  width: 90px;
  height: 90px;
  margin: auto;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.2);
  margin-bottom: 15px;
}

.progress-circle:after {
  position: absolute;
  top: 6px;
  left: 6px;
  display: block;
  content: " ";
  border-radius: 50%;
  background: #111111;
  width: 78px;
  height: 78px;
}

.progress-circle *,
.progress-circle *:before,
.progress-circle *:after {
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
}

.progress-circle > span {
  position: absolute;
  width: 100%;
  z-index: 1;
  left: 0;
  top: 0;
  color: #ffffff;
  line-height: 90px;
  font-size: 15px;
  display: block;
  text-align: center;
}

.progress-circle .slice {
  position: absolute;
  width: 90px;
  height: 90px;
  clip: rect(0px, 90px, 90px, 45px);
}

.progress-circle .bar,
.progress-circle .fill {
  position: absolute;
  border: 6px solid #dcb8b8;
  width: 78px;
  height: 78px;
  clip: rect(0px, 45px, 90px, 0px);
  border-radius: 50%;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -ms-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
}

.pie-fill,
.progress-circle .bar:after,
.progress-circle .fill {
  -webkit-transform: rotate(180deg);
  -moz-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  -o-transform: rotate(180deg);
  transform: rotate(180deg);
}

